import React from "react";
import { Formik, Form } from "formik";
import { useNavigate } from "react-router";
import * as Yup from "yup";

import { StepDocumentsValues, StepperBarItem } from "../../../../types";
import { deliveryFormContext } from "../../../../context";
import AttachInvoice from "../../../../components/attachInvoice/AttachInvoice";
import Input from "../../../../components/input/Input";
import DatePicker from "../../../../components/datePicker/DatePicker";
import NavigationButton from "../../../../components/navigationButton/NavigationButton";
import NavigationLink from "../../../../components/navigationLink/NavigationLink";
import StepperBar from "../../../../components/stepperBar/StepperBar";

import "./documents.scss";

const Documents: React.FC = () => {
	const {
		formState: { documents },
		updateDocuments,
	} = React.useContext(deliveryFormContext);

	const navigate = useNavigate();

	const submitStep = (data: StepDocumentsValues) => {
		updateDocuments(data);
		navigate("/new-order/address");
	};

	const validationSchema = Yup.object().shape({
		invoice: Yup.mixed().required(" -  '."),
		lastName: Yup.string()
			.required("    .")
			.matches(/^[-][-']*$/gi, { message: "     " }),
		firstName: Yup.string()
			.required("    .")
			.matches(/^[-][-']*$/gi, { message: "     " }),
		patronymicName: Yup.string()
			.required("    .")
			.matches(/^[-][-']*$/gi, { message: "     " }),
		passport: Yup.string()
			.required(" .")
			.matches(/[-]{2}\d{6}/gi, { message: "      123456" })
			.max(8, "      123456"),
		birthDate: Yup.string().required("    ."),
		passportIssueDate: Yup.string().required("    ."),
		passportIssuedBy: Yup.string()
			.required("    .")
			.matches(/^[-][-']*$/gi, { message: "     " }),
		registrationAddress: Yup.string()
			.required("    .")
			.matches(/^[-][-']*$/gi, { message: "     " }),
		identificationNumber: Yup.string()
			.required("    .")
			.matches(/\d{10}/gi, { message: "    10 " })
			.max(10, "    10 "),
	});

	const steps: StepperBarItem[] = [
		{ title: "  ", status: "completed", url: "/new-order/generalInformation" },
		{ title: "", status: "editing" },
		{ title: " ", status: "hidden" },
	];

	return (
		<div className="documents-form">
			<div className="documents-form__stepper">
				<StepperBar steps={steps} />
			</div>
			<div className="documents-form__form">
				<Formik initialValues={documents} validationSchema={validationSchema} onSubmit={submitStep}>
					<Form className="documents-form">
						<div className="documents-form__invoice">
							<AttachInvoice
								name="invoice"
								id="input_invoice"
								initialValue={documents.invoice}
								acceptedFormats={["image/jpeg", "image/png", "application/pdf"]}
							/>
						</div>
						<div className="documents-form__row documents-form__row--2-columns">
							<div className="documents-form__column">
								<Input name="lastName" id="input_lastName" label=" ()" />
							</div>
							<div className="documents-form__column">
								<Input name="firstName" id="input_firstName" label="' ()" />
							</div>
						</div>
						<div className="documents-form__row documents-form__row--2-columns">
							<div className="documents-form__column">
								<Input
									name="patronymicName"
									id="input_patronymic-name"
									label="  ()"
								/>
							</div>
							<div className="documents-form__column">
								<Input name="passport" id="input_passport" label="   " />
							</div>
						</div>
						<div className="documents-form__row documents-form__row--2-columns">
							<div className="documents-form__column">
								<DatePicker name="birthDate" id="input_birth-date" label=" " />
							</div>
							<div className="documents-form__column">
								<DatePicker
									name="passportIssueDate"
									id="input_passport-issue-date"
									label="  "
								/>
							</div>
						</div>
						<div className="documents-form__row">
							<Input name="passportIssuedBy" id="input_passport-issued-by" label=" " />
						</div>
						<div className="documents-form__row">
							<Input
								name="registrationAddress"
								id="input_registration-address"
								label=" "
							/>
						</div>
						<div className="documents-form__row">
							<Input
								name="identificationNumber"
								id="input_identification-number"
								label=" "
							/>
						</div>
						<div className="documents-form__row documents-form__row--controls">
							<NavigationLink title="" to="/new-order/generalInformation" />
							<NavigationButton title="" iconPosition="right" type="submit" />
						</div>
					</Form>
				</Formik>
			</div>
		</div>
	);
};

export default Documents;
